*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  color: #1d1613; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.header--opened, .header--nojs {
  background-color: #a87b62; }

.header--nojs .header__toggle {
  visibility: hidden; }

@media (max-width: 1023px) {
  .header--closed .header__search,
  .header--closed .header__navigation {
    display: none; } }

.header--nojs .header__navigation {
  display: block;
  border: none; }

.header--opened .header__logo,
.header--nojs .header__logo {
  color: #ffffff; }

.header--opened .header__toggle-icon,
.header--opened .header__cart-icon,
.header--nojs .header__toggle-icon,
.header--nojs .header__cart-icon {
  stroke: #ffffff; }

.header--opened .header__navigation {
  border: none; }

.header--opened .header__search {
  margin-top: 61px; }

.header__top {
  position: relative;
  max-width: 1366px;
  margin: 0 auto;
  padding: 42px 98px 52px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start; }
  @media (max-width: 1023px) {
    .header__top {
      padding: 55px 45px 45px; } }
  @media (max-width: 767px) {
    .header__top {
      padding: 38px 15px; } }

.header__toggle {
  display: none;
  width: 25px;
  height: 16px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .header__toggle {
      display: block; } }

.header__toggle-icon {
  stroke: #1d1613; }

@media (max-width: 1023px) {
  .header__search {
    display: block;
    width: 100%;
    order: 1; } }

.header__logo {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  color: #a87b62; }
  @media (max-width: 767px) {
    .header__logo {
      top: 20px; } }

.user-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.user-nav__item {
  margin-right: 35px; }
  .user-nav__item:last-child {
    margin-right: 0; }

.user-nav__link {
  transition: color 0.2s ease;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  color: #1d1613; }
  .user-nav__link:hover, .user-nav__link:focus {
    color: #a87b62; }
  .user-nav__link:active {
    color: rgba(168, 123, 98, 0.3); }

.site-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }
  @media (max-width: 1023px) {
    .site-nav {
      flex-direction: column; } }

.site-nav__item {
  margin-right: 35px; }
  .site-nav__item:last-child {
    margin-right: 0; }
  @media (max-width: 1023px) {
    .site-nav__item {
      margin-right: 0;
      margin-bottom: 21px; }
      .site-nav__item:last-child {
        margin-bottom: 0; } }

.site-nav__link {
  transition: color 0.2s ease;
  font-weight: 500;
  line-height: 24px;
  color: #1d1613; }
  .site-nav__link:hover, .site-nav__link:focus {
    color: #a87b62; }
  .site-nav__link:active {
    color: rgba(168, 123, 98, 0.3); }
  @media (max-width: 1023px) {
    .site-nav__link {
      font-size: 24px;
      line-height: 36px;
      color: #ffffff; } }

.additional-nav {
  margin: 0;
  padding: 0;
  list-style: none; }

.additional-nav__item {
  margin-bottom: 20px; }

.additional-nav__link {
  font-size: 18px;
  line-height: 23px;
  color: rgba(255, 255, 255, 0.75); }

.navigation {
  display: flex;
  justify-content: center;
  padding: 19px 0;
  border-top: 1px solid #F6F5F4;
  border-bottom: 1px solid #F6F5F4; }
  @media (max-width: 1023px) {
    .navigation {
      display: block;
      padding: 0 45px 75px; } }
  @media (max-width: 767px) {
    .navigation {
      padding: 0 15px 75px; } }

@media (max-width: 1023px) {
  .navigation__site {
    margin-bottom: 41px; } }

@media (max-width: 767px) {
  .navigation__site {
    margin-bottom: 26px; } }

.navigation__additional {
  display: none; }
  @media (max-width: 1023px) {
    .navigation__additional {
      display: block;
      margin-bottom: 45px; } }
  @media (max-width: 767px) {
    .navigation__additional {
      margin-bottom: 35px; } }

.search__field {
  position: relative;
  display: flex;
  align-items: center; }
  .search__field::before {
    content: '';
    position: absolute;
    width: 14px;
    height: 14px;
    background-image: url("../../img/icons/icon-search.svg");
    background-repeat: no-repeat;
    background-size: cover; }
  @media (max-width: 1023px) {
    .search__field::before {
      left: 12px;
      width: 16px;
      height: 16px;
      background-image: url("../../img/icons/icon-search-mobile.svg"); } }

.search input {
  padding: 0 0 0 29px;
  border: none;
  font-family: inherit;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px; }
  .search input::placeholder {
    color: #9b9997; }
  @media (max-width: 1023px) {
    .search input {
      width: 100%;
      height: 40px;
      padding-left: 40px;
      background-color: rgba(255, 255, 255, 0.15);
      border-radius: 20px; }
      .search input::placeholder {
        color: transparent; } }

.login--mobile {
  display: none; }

@media (max-width: 1023px) {
  .login {
    font-size: 18px;
    line-height: 23px; }
    .login--desktop {
      display: none; }
    .login--mobile {
      display: block;
      color: #ffffff; } }

@media (max-width: 1023px) {
  .header-cart__text {
    display: none; } }

.header-cart__icon {
  display: none;
  stroke: #1d1613; }
  @media (max-width: 1023px) {
    .header-cart__icon {
      display: block; } }
